$(function(){

const modalBtn = document.querySelectorAll('[data-modal]');
const volumeBtn = document.querySelectorAll('.volume__btn');
const modalClose = document.querySelectorAll('.modal__close');
const modalClose2 = document.querySelectorAll('.modal__grat_btn');
const modal = document.querySelectorAll('.modal');
const body = document.body;


modalBtn.forEach(item => {
  item.addEventListener('click', event =>{
    let $this = event.currentTarget;
    let modalId = $this.getAttribute('data-modal');
    let modal = document.getElementById(modalId);
    let modalContent = modal.querySelector('.modal__content');

    modalContent.addEventListener('click',event => {
      event.stopPropagation();
    });

    modal.classList.add('show');
    body.classList.add('no-scroll');

    setTimeout(()=>{

      modalContent.style.transform = 'none';
    }, 1);


  });
});

modalClose.forEach(item => {
  item.addEventListener('click', event =>{
    let currentModal = event.currentTarget.closest('.modal');

    closeModal(currentModal);

  });
});

modalClose2.forEach(item => {
  item.addEventListener('click', event =>{
    let currentModal = event.currentTarget.closest('.modal');

    closeModal(currentModal);

  });
});

modal.forEach(item => {
  item.addEventListener('click', event =>{
    let currentModal = event.currentTarget;
    closeModal(currentModal);
   

  });
});

function closeModal(modal){
  let modalContent = modal.querySelector('.modal__content');
  modalContent.style.transform = 'rotateX(90deg)';
  setTimeout(()=>{
    modal.classList.remove('show');
  body.classList.remove('no-scroll');
  }, 500);
  

}

volumeBtn.forEach(item => {
  item.addEventListener('click', event =>{
    let $this = event.currentTarget;
    let imglId = $this.getAttribute('data-volume');
    console.log(imglId);
    let volumeImg = document.querySelector('.volume').querySelector('.volume__img');
    
    volumeImg.setAttribute('src',imglId);

  });
});


//Slider

$('[data-slider="slick"]').slick({
  infinite: true,
  slidesToShow: 1,
  slidesToScroll: 1,
  fade:true,
  arrows: false
});

$(".next").on("click",function(event){
  event.preventDefault();

  let currentSlider = $(this).parents(".reviews").find('[data-slider="slick"]');
  
  currentSlider.slick("next");
});

$(".preview").on("click",function(event){
  event.preventDefault();
  let currentSlider = $(this).parents(".reviews").find('[data-slider="slick"]');
  currentSlider.slick("preview");
});

});